.MobileNavigation {
	display: none;
	width: 100%;
	justify-content: space-between;
}

.MobileNavigation_menu {
	position: absolute;
	width: 100%;
	min-height: calc(100vh - var(--height_header_mobile));
	background-color: var(--color_main_1);
	left: 0;
	top: var(--height_header_mobile);
	z-index: 1000;
	padding: 48px;
	padding-left: 32px;
}

.MobileNavigation_menu.closed {
	display: none;
}

.MobileNavigation_menu.open {
	display: block;
}

.MobileNavigation_button {
	background-color: transparent;
	border-radius: 8px;
	border-style: none;
	width: 80px;
	height: 80px;
}

.MobileNavigation_button:hover {
	background-color: var(--color_main_3);
}

.MobileNavigation_button:active {
	background-color: var(--color_main_1);
}

.MobileNavigation_menu_buttons {
	display: grid;
	padding-bottom: 48px;
}


@media (max-width: 1023px) {
	.MobileNavigation {
		display: flex;
	}
}