.Frame {
	min-height: 100vh;
	height: auto;
}

.Frame_content {
	min-height: calc(100vh - var(--height_header));
	/* overflow: auto; */
	padding-top: var(--height_header);
}

@media (max-width: 1023px) {
	.Frame_content {
		height: calc(100vh - var(--height_header_mobile));
		padding-top: var(--height_header_mobile);
	}
}