.Portfolio h1 {
	font-family: 'Roboto', sans-serif;
	margin-top: 0;
	margin-bottom: 96px;
}


.Portfolio_back {
	margin-top: 96px;
}

.Portfolio_back_content {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}

.Portfolio_button_arrow {
	position: absolute;
	width: 32px;
	height: 32px;
	left: -16px;
}

@media (max-width: 599px) {
	.Portfolio h1 {
		margin-bottom: 64px;
		margin-left: 48px;
	}
}