.LanguageSelector_button {
	text-decoration: underline;
	padding: 16px;
	border-radius: 8px;
	width: 64px;
	font-size: 16px;
}


.LanguageSelector_button:hover {
	background-color: rgba(0, 0, 0, 0.205);
}

.LanguageSelector_button:active {
	background-color: rgba(0, 0, 0, 0.39);
	transform: scale(1);
	color: var(--color_text_6);

	transform: scale(1);
	transition: transform 0.1s;
	transition-timing-function: ease-out;
}
.LanguageSelector_button:hover {
	transition: transform 0.1s;
	transition-timing-function: ease-out;
	transform: scale(1.1);
}


@media (prefers-reduced-motion) {
	.LanguageSelector_button:hover{
		transition: none;
		transform: initial;
	}
	.LanguageSelector_button:active {
		transition: none;
		transform: initial;
	}
}

@media (max-width: 1023px) {
	.LanguageSelector_button {
		text-align: left;
		width: auto;
		padding-left: 32px;
	}
}