.Main {
	min-height: calc(100vh - var(--height_header) - var(--height_footer));
}

.Main_content {
	width: var(--width_main_0);
	padding: 64px;
	padding-top: 128px;
	padding-bottom: 128px;
	min-height: 100%;
	margin: auto;
}

@media (max-width: 1279px) {
	.Main_content {
		width: var(--width_main_1);
	}
}

@media (max-width: 1023px) {
	.Main_content {
		width: 100%;
	}

	.Main {
		min-height: calc(100vh - var(--height_header_mobile) - var(--height_footer));
	}
}

@media (max-width: 599px) {
	.Main_content {
		padding-left: 16px;
		padding-right: 16px;
		padding-top: 64px;
		padding-bottom: 64px;
	}
}