.Header {
	background-color: var(--color_main_2);
	height: var(--height_header);
	box-shadow: 0 0 32pt 0pt rgba(0, 0, 0, 0.459);
	z-index: 2000;
	position: fixed;
	width: 100vw;
	top: 0;
}

.Header_content {
	display: flex;
	align-items: center;
	width: var(--width_main_0);
	height: 100%;
	margin: auto;
	padding-left: 64px;
	padding-right: 64px;
	justify-content: space-between;
}

@media (max-width: 1279px) {
	.Header_content {
		width: var(--width_main_1);
	}
}

@media (max-width: 1023px) {
	.Header {
		height: var(--height_header_mobile);
	}

	.Header_content {
		width: 100%;
		padding-left: 32px;
		padding-right: 16px;
	}

	.Header_language_selector {
		display: none;
	}
}