.Contact h1 {
	margin-top: 0;
	margin-bottom: 96px;
	font-family: "Roboto", sans-serif;
}

.Contact h2 {
	margin-bottom: 16px;
	margin-top: 64px;
	font-size: 16px;
	color: var(--color_text_6);
}

.Contact p {
	font-size: 24px;
}

.Contact a {
	color: var(--color_accent_5);
	font-size: 24px;
}

.Contact a:hover {
	color: var(--color_accent_6)
}

.Contact a:active {
	color: var(--color_accent_4)
}

.Contact_back {
	margin-top: 128px;
}


@media (max-width: 1023px) {

}

@media (max-width: 599px) {
	.Contact h1 {
		margin-left: 16px;
	}

	.Contact p {
		font-size: 18px;
	}

	.Contact a {
		font-size: 18px;
	}
}