.Project h1 {
	font-family: "Roboto", sans-serif;
	margin: 0;
}

.Project h2 {
	color: var(--color_text_6);
	font-size: 20px;
	margin-top: 8px;
	margin-bottom: 32px;
}

.Project_widgets {
	display: flex;
	gap: 8px;
	flex-wrap: wrap;
	margin-bottom: 64px;
}

.Project_back {
	margin-top: 96px;
}

.Project p {
	font-size: 20px;
	/* text-align: justify; */
	hyphens: auto;
}

.Project .ImageWheel {
	margin-bottom: 48px;
}

@media (max-width: 1023px) {
	.Project h2 {
		font-size: 16px;
	}
}