.ImageWheel {
	width: 100%;
	overflow: hidden;
	border-radius: 8px;
	position: relative;
}

.ImageWheel_container {
	display: grid;
    transition: 0.5s ease-out;
	overflow: visible;
	align-items: center;
}

.ImageWheel_container img {
	width: 100%;
	height: auto;
	/* display: inline-block; */
	/* position: absolute; */
}

.ImageWheel_button {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 64px;
	border-style: none;
	background-color: transparent;
	padding: 0;
}

.ImageWheel_button:hover {
	background-color: rgba(0, 0, 0, 0.5);
}

.ImageWheel_button.right {
	left: calc(100% - 64px);
}

.ImageWheel_icon_container {
	display: flex;
	position: absolute;
	top: calc(100% - 32px);
	gap: 8px;
	justify-content: center;
	width: 100%;
}

.ImageWheel_icon {
	background-color: white;
	border-radius: 50%;
	width: 8px;
	height: 8px;
}

.ImageWheel_icon.selected {
	background-color: var(--color_accent_5);
	border-radius: 50%;
	width: 8px;
	height: 8px;
}

@media (prefers-reduced-motion) {

	.ImageWheel_container {
		transition: none;
	}
}