.Home_header {
	display: flex;
	gap: 80px;
	align-items: center;
	margin-bottom: 80px;
}

.Home_header img {
	width: 160px;
	height: 160px;
	margin: -6px;
}

.Home_header_title h1 {
	margin-top: 0;
	font-family: 'Roboto', sans-serif;
	font-size: 64px;
	margin-bottom: 8px;
}

.Home_header_title h2 {
	margin-bottom: 0;
	margin-top: 0;
	font-size: 26px;
	color: var(--color_text_6);
}

.Home h3 {
	font-family: 'Roboto', sans-serif;
	margin-top: 128px;
	margin-bottom: 64px;
	font-size: 26px;
}

.Home_about_text {
	font-family: 'Montserrat', sans-serif;
	font-size: 20px;
	color: var(--color_text_7);
	/* text-align: justify; */
	hyphens: auto;
}

.Home_read_more {
	margin-top: 64px;
	width: 100%;
	justify-content: right;
	display: flex;
}


.Home_more_projects {
	margin-top: 64px;
	width: 100%;
	justify-content: right;
	display: flex;
}

.Home_more_projects_content {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}

.Home_button_arrow {
	position: absolute;
	width: 32px;
	height: 32px;
	left: 100%;
}


@media (max-width: 1023px) {
	.Home_header {
		gap: 32px;
		align-items: center;
		margin-bottom: 80px;
	}

	.Home_header img {
		width: 128px;
		height: 128px;
	}
	.Home_header_title h1 {
		font-size: 38px;
	}

	.Home_header_title h2 {
		font-size: 20px;
	}

	.Home_about_text {
		font-size: 18px;
	}
}

@media (max-width: 599px) {
	.Home_header {
		flex-direction: column;
	}

	.Home_header img {
		width: 128px;
		height: 128px;
	}

	.Home_header_title h1 {
		font-size: 32px;
	}

	.Home h3 {
		font-size: 24px;
		margin-left: 16px;
		margin-right: 16px;
	}
}