@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

/* * {
    font-family: 'Montserrat', sans-serif;
} */

:root {
	--color_main_0: rgb(16, 16, 17);
	--color_main_1: rgb(21, 21, 23);
	--color_main_2: rgb(26, 26, 27);
	--color_main_3: rgb(36, 36, 37);

	--color_accent_8: rgb(199, 253, 232);
	--color_accent_6: rgb(134, 253, 208);
	--color_accent_5: rgb(18, 241, 156);
	--color_accent_4: rgb(2, 182, 113);

	--color_accent_gradient_5: rgb(20, 245, 196);
	--color_accent_gradient_4: rgb(2, 182, 143);

	--color_text_8: rgb(245, 245, 253);
	--color_text_7: rgb(201, 201, 209);
	--color_text_6: rgb(152, 152, 158);
	--color_text_4: rgb(73, 73, 77);

	--height_header: 128px;
	--height_header_mobile: 96px;
	--height_footer: 96px;

	--width_main_0: 1200px;
	--width_main_1: 1024px;

	--breakpoint_0: 1920px;
	--breakpoint_1: 1024px;
	--breakpoint_2: 600px;
}

body {
  	margin: 0;
	font-family: 'Montserrat', sans-serif;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
	color: var(--color_text_8);
	background-color: var(--color_main_0);
}

code {
  	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div, main {
	box-sizing: border-box;
}