.About_content {
	display: grid;
	grid-template-columns: 1fr 3fr;
	gap: 32px
}

.About_content_text {
	display: grid;
	gap: 32px;
}

.About_back {
	margin-top: 128px;
}

.About h1 {
	margin-top: 0;
	margin-bottom: 96px;
	font-family: "Roboto", sans-serif;
}

.About h2 {
	font-family: "Roboto", sans-serif;
	margin-top: 96px;
	margin-bottom: 64px;
}

.About p {
	font-size: 20px;
	/* text-align: justify; */
	margin: 0;
	hyphens: auto;
}

.About_image {
	width: 350px;
	/* float: left; */
	border-radius: 8px;
}

.About_table {
	display: grid;
	grid-template-columns: 1fr 5fr;
	width: 100%;
	border-radius: 8px;
	overflow: hidden;
}

.About_table_header {
	border-right-style: solid;
	border-width: 1px;
	border-color: var(--color_text_4);
	padding: 16px;
}

.About_table_header:nth-child(odd of .About_table_header) {
	background-color: var(--color_main_2);
}

.About_table_header.light {
	background-color: var(--color_main_2);
}

.About_table_cell {
	padding: 16px;
	padding-left: 32px;
}

.About_table_cell:nth-child(odd of .About_table_cell) {
	background-color: var(--color_main_2);
}

.About_table_cell.light{
	background-color: var(--color_main_2);
}


@media (max-width: 1023px) {
	.About_content {
		display: flex;
		flex-direction: column;
	}

	.About_image {
		margin: auto;
	}
}

@media (max-width: 599px) {
	.About_image {
		width: 100%;
	}

	.About h1 {
		margin-left: 16px;
		margin-bottom: 64px;
	}

	.About_back {
		margin-top: 96px;
	}
}