.ProjectThumbnail {
	background-color: var(--color_main_1);
	border-radius: 8px;
	padding: 0;
	color: var(--color_text_8);
	border-style: none;
	cursor: pointer;
	overflow: hidden;
	text-align: left;

	box-shadow: 0 8px 16px 8px rgba(0, 0, 0, 0.2);

	transition: 0.25s ease-out;

	height: 400px;
	width: 100%;
	position: relative;
}

.ProjectThumbnail:hover {
	transform: scale(1.05);
	transition: 0.25s ease-out;
}

.ProjectThumbnail_header {
	background-color: var(--color_main_2);
	padding: 24px;
	padding-left: 48px;
	padding-right: 48px;
}

.ProjectThumbnail_header h4{
	margin: 0;
	margin-bottom: 8px;
	font-size: 30px;
	font-family: 'Roboto', sans-serif;
}

.ProjectThumbnail_header h5 {
	margin: 0;
	color: var(--color_text_6);
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
}

.ProjectThumbnail_content {
	position: relative;
	width: 100%;
	overflow: hidden;
}

.ProjectThumbnail_description {
	height: 100%;
	width: 100%;
	top: 400px;
	/* top: 0; */
	position: absolute;
	left: 0;
	overflow: hidden;
	transition: 0.25s ease-out;
	padding: 32px;
	background-color: var(--color_main_1);
	font-size: 16px;
	font-family: 'Montserrat', sans-serif;
	hyphens: auto;
	text-align: justify;
	/* display: flex;
	flex-direction: column;
	justify-content: space-between; */
}

.ProjectThumbnail_description p {
	margin-top: 0;
}

.ProjectThumbnail:hover .ProjectThumbnail_content .ProjectThumbnail_description {
	top: 0;
	transition: 0.25s ease-out;
}

.ProjectThumbnail_widgets {
	position: absolute;
	top: calc(100% - 64px + 400px);
	padding-left: 32px;
	transition: 0.25s ease-out;
}

.ProjectThumbnail:hover .ProjectThumbnail_widgets {
	top: calc(100% - 64px);
	transition: 0.25s ease-out;
}

.HerpThumbnail_image {
	margin-left: -155px;
	margin-top: -150px;
	/* position: absolute; */
}

.KamkballThumbnail_image {
    margin-left: -1100px;
    margin-top: -500px;
	/* position: absolute; */
}

.VienaThumbnail_image {
	margin-left: -50px;
	margin-top: -10px;
	width: 600px;
}

.IroninvasionThumbnail_image {
	margin-left: -750px;
	margin-top: -500px;
}


@media (prefers-reduced-motion) {

	.ProjectThumbnail {
		transition: none;
	}

	.ProjectThumbnail:hover {
		transform: none;
		transition: none;
	}

	.ProjectThumbnail_description {
		transition: none;
	}
	
	.ProjectThumbnail:hover .ProjectThumbnail_content .ProjectThumbnail_description {
		transition: none;
	}

	.ProjectThumbnail_widgets {
		transition: none;
	}
	
	.ProjectThumbnail:hover .ProjectThumbnail_widgets {
		transition: none;
	}
}

@media (max-width: 599px) {
	.ProjectThumbnail_header {
		padding: 24px;
		padding-left: 24px;
		padding-right: 24px;
	}

	.ProjectThumbnail_description {
		display: none;
	}

	.ProjectThumbnail_widgets {
		display: none;
	}
}