.TechnologyWidget div {
	padding: 8px;
	padding-left: 16px;
	padding-right: 16px;
	font-family: "Roboto", sans-serif;
	color: var(--color_text_8);
	border-radius: 50px;
	font-size: 16px;
}

.TechnologyWidgetContainer {
	display: flex;
	gap: 8px;
}

.ReactWidget {
	background-color: rgb(24, 97, 192);
}

.DjangoWidget {
	background-color: rgb(17, 138, 87);
}

.PostgreWidget {
	background-color: rgb(192, 72, 24);
}

.AwsWidget {
	background-color: rgb(155, 126, 0);
}

.UnityWidget {
	background-color: rgb(141, 10, 153);
}

.PhpWidget {
	background-color: rgb(7, 122, 122);
}

.MySqlWidget {
	background-color: rgb(10, 153, 10);
}

.AndroidWidget {
	background-color: rgb(128, 18, 42);
}

.CoreWidget {
	background-color: rgb(153, 103, 10);
}