.Button {
	border-style: none;
	color: var(--color_text_8);
	background-color: transparent;
	font-size: 16px;
	padding: 0;
}

.Button:hover {
	cursor: pointer;
}

.NavigationButton {
	padding: 16px;
	border-radius: 8px;
}

.Button:hover .NavigationButton {
	background-color: rgba(0, 0, 0, 0.205);
}

.Button:active .NavigationButton {
	background-color: rgba(0, 0, 0, 0.39);
}

.NavigationButton_content {
	border-bottom: 0px;
	border-bottom-style: solid;
	border-top: 0px;
	border-top-style: solid;

	transition: transform 0.1s;
	transition-timing-function: ease-out;

	box-sizing: content-box;

	text-decoration: underline;
}

.NavigationButton.selected .NavigationButton_content {
	/* border-bottom: 2px; */
	border-bottom-style: solid;
	text-decoration: none;
	transition: transform 0.1s;
	transition-timing-function: ease-out;
	/* color: var(--color_accent_6); */
}

.Button:hover .NavigationButton {
	transition: transform 0.1s;
	transition-timing-function: ease-out;
	transform: scale(1.1);
}

.Button:hover .NavigationButton .NavigationButton_content {
	/* border-bottom: 0px; */
	border-bottom-style: solid;
	border-top-style: solid;
	/* transition: transform 0.1s;
	transition-timing-function: ease-out;
	transform: scale(1.1); */
}

.Button:active .NavigationButton .NavigationButton_content {
	/* transform: scale(1); */
	color: var(--color_text_6)
}


.SecondaryButton {
	padding: 24px;
	padding-left: 48px;
	padding-right: 48px;
	border-radius: 64px;
	background-color: var(--color_accent_4);
	color: black;
	min-width: 256px;
	margin-left: -4px;
	margin-right: -4px;
	background-image: linear-gradient(45deg, var(--color_accent_4), var(--color_accent_gradient_4));
	/* background-color: black; */
	transition: 0.1s ease-out;
	box-shadow: 0 8px 16px -4px rgba(0, 0, 0, 0.5);
}

.SecondaryButton:hover {
	transition: 0.1s ease-out;
	transform: scale(1.1);
	background-image: linear-gradient(45deg, var(--color_accent_5), var(--color_accent_gradient_5));
}

.SecondaryButton:active {
	background-image: linear-gradient(45deg, var(--color_accent_4), var(--color_accent_gradient_4));
}




.TertiaryButton {
	padding: 24px;
	padding-left: 48px;
	padding-right: 48px;
	border-radius: 64px;
	background-color: var(--color_main_0);
	color: var(--color_text_8);
	min-width: 256px;
	margin-left: -4px;
	margin-right: -4px;
	transition: 0.1s ease-out;
	box-shadow: 0 8px 16px -4px rgba(0, 0, 0, 0.5);
	border-style: solid;
	border-width: 1px;
	border-color: var(--color_text_8);



	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}

.TertiaryButton:hover {
	transition: 0.1s ease-out;
	transform: scale(1.1);
	background-color: var(--color_main_1);
}

.TertiaryButton:active {
	background-color: black;
}

.TertiaryButton_button_arrow {
	position: absolute;
	width: 32px;
	height: 32px;
	left: 16px;
}

@media (prefers-reduced-motion) {
	
	.NavigationButton_content {
		transition: none;
	}

	.Button:hover .NavigationButton {
		transition: none;
		transform: initial;
	}

	.Button:hover .NavigationButton .NavigationButton_content {
		transition: none;
		transform: initial;
	}
	
	.NavigationButton.selected .NavigationButton_content {
		transition: none;
	}

	.SecondaryButton {
		transform: none;
		transition: none;
	}
	.SecondaryButton:hover {
		transform: none;
		transition: none;
	}

	.TertiaryButton {
		transform: none;
		transition: none;
	}
	.TertiaryButton:hover {
		transform: none;
		transition: none;
	}
}


@media (max-width: 1023px) {

	.NavigationButton {
		padding: 24px;
		padding-left: 32px;
		border-radius: 8px;
		font-size: 20px;
		text-align: left;
	}
}


@media (max-width: 599px) {
	.Button {
		width: 100%;
	}

	.SecondaryButton {
		width: 100%;
		margin: 0;
	}

	.TertiaryButton {
		width: 100%;
		margin: 0;
	}
}